/* This file contains SCSS for the home page pitch */

.pitch {
  grid-area: pitch;

  /* This places all the pitch-entries in pitch on a row  */
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: max-content;
  overflow: hidden;
  grid-column-gap: 20px;

}

/* Pitch entry */

.pitch-entry-icon {
  grid-area: pitch-entry-icon;
}

.pitch-entry-icon-image {
  width: 100%;
  height: 100%;
}

.pitch-entry-title {
  grid-area: pitch-entry-title;
  padding-left: 5%;
  /* This is what we use to make text in center of column */
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-weight: 900;
  font-size: 1.5vw;
}

.pitch-entry-text {
  grid-area: pitch-entry-text;
  hyphens: auto;

  white-space: normal;
  word-break: break-word;
  font-size: 1vw;

  margin-bottom: 30px;

  /*overflow-wrap: break-word; */
}

.pitch-entry {
  grid-row-gap: 20px;
  display: grid;
  grid-template-areas:
  "pitch-entry-icon pitch-entry-title"
  "pitch-entry-text pitch-entry-text";

  grid-template-columns: 35% 65%;
}


@media only screen and (max-width: 800px) and (min-width: 0px) {
  .pitch { 
    grid-template-columns: 100%; 
    grid-template-rows: 1fr 1fr 1fr;
  }

  .pitch-entry {
    grid-template-columns: 15% 85%;
  }

  .pitch-entry-title {
    font-size: 5vw;
  }

  .pitch-entry-text {
    font-size: 3vw;
  }
}


